.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsName{
    background-color: #31394c;

}

.dialogsMessage{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows:20fr 3fr ;
    justify-content: space-between;

}

.message{
    padding: 10px;
    text-align: left;

}
.right{
    text-align: right;
}

.message span{
    background-color: #31394c;
    padding: 5px;
    border-radius: 5px;
    box-shadow: -0.2px 2px 6px 0 rgba(0, 0, 0, 0.06);
}

.item a {
    color: rgba(255, 255, 255, 0.6);
    text-decoration: none;
    font-size: 13px;
    font-weight: 500;

}

.item a.active {
    color: #fff;
}

.item {
    display: grid;
    grid-template-columns: 40px 1fr;
    padding: 10px;
    align-items: center;
}


.photo {
    width: 30px;
    height: 30px;
    border-radius: 50%;
}

